import React, { useState } from 'react';
import styled from 'styled-components';
import { keyframes } from 'styled-components';
import Modal from 'styled-react-modal';
import { ModalProvider } from 'styled-react-modal';
import { ListItem, Buttons } from './Common';

const Opacity = keyframes`
	from {opacity:0;}
	to {opacity:1;}
`;

const StyledModal = Modal.styled`
  width: 100vw;
  height: 100vh;
  padding: 4rem;
  background-color: #000;
	animation-name: ${Opacity};
	animation-duration: 0.5s;
`;
const X = styled.button`
  border: 0;
  outline: 0;
  cursor: pointer;
  background: none;
  width: 3rem;
  height: 3rem;
  font-size: 3rem;
  color: #bcbcbc;
  font-family: 'Yj';
  transform: scale(1.5, 1);
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  z-index: 9;
`;

const Content = styled.div`
  height: calc(100% - 0.5rem);
`;

const ButtonLink: React.FC<{
  title: string;
  description: string;
  onClick?: () => void;
}> = ({ title, description, ...rest }) => {
  return (
    <div className="link-button" {...rest}>
      <div style={{ fontSize: '1.5rem' }}>{title}</div>
      <div
        style={{ fontSize: '0.9rem', marginTop: '1rem', fontFamily: 'Score' }}
      >
        {description}
      </div>
    </div>
  );
};

const Videos: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const toggleModal = () => setIsOpen(!isOpen);
  const [src, setSrc] = useState('');

  return (
    <ListItem>
      <div
        style={{
          fontFamily: 'Yspot',
          fontSize: '1.5rem',
        }}
      >
        빅데이터 <span style={{ color: '#559043' }}>개념 이해 영상</span>
      </div>
      <ModalProvider>
        <StyledModal isOpen={isOpen} onEscapeKeydown={toggleModal}>
          <X onClick={toggleModal}>X</X>
          <Content>
            {src.includes('youtube') ? (
              <iframe
                src={src}
                title="YouTube video player"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
                frameBorder="0"
                width="100%"
                height="100%"
              />
            ) : (
              <video controls autoPlay width="100%" height="100%">
                <source src={src} />
              </video>
            )}
          </Content>
        </StyledModal>
      </ModalProvider>
      <Buttons backgroundColor="#559043">
        <ButtonLink
          title="빅데이터"
          description="처음 접하는 빅데이터, 빅데이터란 무엇인가요?"
          onClick={() => {
            setSrc(
              `https://www.youtube-nocookie.com/embed/6MrCdXBlmqM?autoplay=1`
            );
            toggleModal();
          }}
        ></ButtonLink>
        <ButtonLink
          title="추천 알고리즘"
          description="추천 알고리즘은 어떻게 만들어지는 것일까?"
          onClick={() => {
            setSrc(
              `https://wstrssl.ebsmath.co.kr/ebsvod/ebsm/M41M2001/M202000009/M202000009_1000K_M00123.mp4`
            );
            toggleModal();
          }}
        />
        <ButtonLink
          title="빅데이터 활용 사례"
          description="스마트 과학 콘텐츠 구축사업을 통해 알아보자!"
          onClick={() => {
            setSrc(
              `https://www.youtube-nocookie.com/embed/vyRC1bUAwFE?autoplay=1`
            );
            toggleModal();
          }}
        />
      </Buttons>
    </ListItem>
  );
};
export default Videos;
